.home {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row-reverse;
  margin: 0 40px;

  img {
    max-width: 360px;
    margin-left: 30px;
    box-shadow: 5px 5px 0px 0px $color-3;

    &:hover {
      // background-color: $black;
      transform: translate(7px, -7px) !important;
    }
  }

  p {
    padding-right: 40px;
    max-width: 740px;
  }

  .Cursor {
    font-weight: 900;
    font-size: 58px;
  }

  .Typist {
    white-space: nowrap;
    font-weight: 900;
    font-size: 58px;
  }

  .Typist .Cursor--blinking {
    padding: 5px;
  }

  .under-title {
    margin: 20px 0;
    font-weight: 900;
    font-size: 32px;
    color: $color-3;
  }

  .type-name {
    color: $color-1;
  }

  .home-main {
    display: flex;
    flex-direction: column;

    a {
      margin-top: 75px;
      font-family: $font-family1;
      font-weight: 900;
      display: flex;
      background: $color-3;
      border-radius: 12px;
      border: none;
      padding: 0;
      cursor: pointer;
      outline-offset: 4px;
      width: 197px;
    }

    .home-button {
      padding: 12px 42px;
      border-radius: 12px;
      font-size: 20px;
      background: $color-1;
      color: white;
      transform: translateY(-6px);
    }

    a:active .home-button {
      transform: translateY(-2px);
    }
  }

  @media screen and (max-width: 980px) {
    height: unset;
    flex-direction: column;
    margin: unset;

    img {
      margin-bottom: 60px;
      max-width: 280px;
    }

    .home-main {
      align-items: center;
    }

    p {
      padding-right: unset;
      text-align: center;
      font-size: 20px;
    }
  }

  @media screen and (max-width: 680px) {
    img {
      max-width: 200px;
      margin-left: unset;
    }

    .Typist {
      font-size: 34px;
    }

    .Typist .Cursor--blinking {
      font-size: 30px;
      padding: 5px;
    }

    .under-title {
      text-align: center;
      margin: 20px auto;
      font-size: 24px;
    }
  }

  @media screen and (max-width: 350px) {
    img {
      max-width: 100px;
    }

    .Typist {
      font-size: 28px;
    }

    .Typist .Cursor--blinking {
      font-size: 30px;
      padding: 5px;
    }

    .under-title {
      text-align: center;
      margin: 20px auto;
      font-size: 24px;
    }

    .home-main a {
      font-size: 20px;
      margin-top: 20px;
    }

    .home-main .icon {
      font-size: 20px;
      margin-left: 10px;
      position: relative;
      top: 1px;
    }

    p {
      padding: 10px;
    }

    a {
      width: 163px !important;
    }

    .home-button {
      font-size: 14px !important;
      width: 163px !important;
    }
  }
}
